<template>
  <div>
    <div style="font-size: 16px; position: absolute; top: 12px; right: 40px">
      <el-link
        :underline="false"
        type="primary"
        style="font-size: 16px; cursor: pointer; margin-top: 10px"
        @click="addBooks"
        ><i class="iconfont iconxinjian" style="padding-right: 5px"></i
        >新增</el-link
      >
    </div>

    <div id="top">
      <div>
        <span>ISBN</span>
        <el-input
          v-model="params.isbn"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span>书名</span>
        <el-input
          v-model="params.name"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span>作者</span>
        <el-input
          v-model="params.author"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <el-button-group style="margin-left: 20px">
          <el-button type="primary" size="mini" @click="search">搜索</el-button>
          <el-button size="mini" @click="resetTable">重置</el-button>
        </el-button-group>
      </div>
    </div>

    <el-table
      v-loading="loading"
      ref="multipleTable"
      :data="tableData"
      size="small"
      border
      :height="height - 130"
      style="width: 100%; text-align: center; line-height: 18px"
    >
      <el-table-column
        label="ISBN10"
        align="center"
        prop="isbn10"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="ISBN13"
        align="center"
        prop="isbn13"
        width="150"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="书名"
        align="center"
        prop="title"
        show-overflow-tooltip
      >
      </el-table-column>
      <el-table-column
        label="作者"
        align="center"
        prop="author"
        show-overflow-tooltip
      >
      </el-table-column>
      <el-table-column
        label="出版社"
        align="center"
        prop="publisher"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="简介"
        align="center"
        prop="summary"
        show-overflow-tooltip
      >
        <template slot-scope="{ row }">
          <el-button type="text" @click="showInfo(row)">查看</el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="价格"
        align="center"
        prop="price"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="封面"
        align="center"
        prop="image"
        show-overflow-tooltip
      >
        <template slot-scope="{ row }">
          <el-image
            style="height: 100px"
            :src="row.image"
            fit="cover"
          ></el-image>
        </template>
      </el-table-column>
      <el-table-column
        label="装订"
        align="center"
        prop="binding"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column label="操作" align="center" width="100">
        <template slot-scope="{ row }">
          <el-link type="warning" @click="del(row)" disabled>删除</el-link>
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 150, 200]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      style="float: right; margin-top: 20px"
    >
    </el-pagination>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogFormVisible"
      width="650px"
      :close-on-click-modal="false"
      :append-to-body="true"
      @closed="resetForm"
    >
      <el-form
        label-position="top"
        label-width="80px"
        :model="form"
        ref="ruleForm"
        size="mini"
      >
        <el-form-item label="isbn">
          <el-input v-model="form.isbn"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm" size="mini">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')" size="mini"
          >提 交</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "book_management",
  components: {},
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loading: false,
      currentPage: 1, //当前页码
      pageSize: 20,
      total: 0,
      params: {
        name: "",
        isbn: "",
        author: "",
      },
      tableData: [],
      dialogFormVisible: false,
      dialogTitle: "管理",
      form: {
        isbn: "",
      },
    };
  },
  created() {},
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$reqGet("mall/books/all").then((res) => {
        if (res.code == 200) {
          this.tableData = res.data;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$reqPost("/ali/getInfoByIsbn", {
            ...this.form,
          }).then((res) => {
            if (res.code == 200) {
              if (res.data) this.dialogFormVisible = false;
              this.getData();
            } else {
              this.$message.error(res.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    resetForm() {
      this.$refs["ruleForm"].resetFields();
      this.form = {
        isbn: "",
      };
      this.getData();
    },
    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    handleSelectionChange(val) {
      console.log(val);
    },
    search() {
      console.log(this.params);
      this.getData();
    },
    resetTable() {
      this.params = {
        openid: "",
        id: "",
        phoneNumber: "",
        status: "",
      };
      this.getData();
    },
    del(row) {
      console.log(row);
    },
    showInfo(row) {
      this.$alert(row.summary, "简介", {
        confirmButtonText: "确定",
        customClass: "summary",
      });
    },
    addBooks() {
      this.dialogFormVisible = true;
    },
  },
};
</script>
<style scoped>
#top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 14px;
  color: #595959;
}
.search {
  width: 140px;
  margin-left: 10px;
  margin-right: 10px;
}
</style>
<style>
.summary {
  width: 800px;
}
</style>
